@mixin font-face($name, $file, $weight: 400, $style: normal) {
	@font-face {
		font-family: "#{$name}";
		src: local("#{$file}"),
		url('../fonts/#{$file}.woff2') format('woff2'),
		url('../fonts/#{$file}.woff') format('woff');
		font-weight: $weight;
		font-style: $style;
		font-display: swap;
	}
}

@mixin font($family, $size) {
	font-family: ProximaNova- + $family;
	font-size: $size + px;
}

@mixin adaptive-value($property, $startSize, $minSize) {
	#{$property}: $startSize + px;
	@media (max-width: #{$max-container-size + px}) {
		#{$property}: calc(#{$minSize + px} + #{$startSize - $minSize} * ((100vw - 320px) / #{$max-container-size - 320}));
	}
}