// colors
$orange: #FFB51B;
$orange-add: #FFD278;
$blue: #3A87FE;
$blue-hover: #5096ff;
$green: #76BB40;
$green-add: #3DB39E;
$green-add-hover: #53ccb6;

//sizes
$layout-width: 1920;
$max-container-size: 1436;
$md1: 1500;
$md2: 1200;
$md3: 992; 
$md4: 768;
$md5: 480;
$md6: 320;

body {
    background: url('../img/background/big/1.svg') no-repeat,
                url('../img/background/big/2.svg') no-repeat,
                url('../img/background/big/3.svg') no-repeat;
    background-position: right top,
                         left 252px,
                         right 1108px;
    background-size: calc(1222 / 1920 * 100%),
                     calc(296 / 1920 * 100%),
                     calc(837 / 1920 * 100%);
    @include font(Regular, 26);
}

.container {
    max-width: $max-container-size + px;
    margin: 0 auto;
    @media (max-width: $md1 + px) {
        max-width: 1100px;
    }
    @media (max-width: $md2 + px) {
        padding: 0 15px;
    }
}

.button {
    background: $blue;
    &:hover {
        background: $blue-hover;
    }
    color: #fff;
    font-family: ProximaNova-Bold;
    // font-size: 30px;
    @include adaptive-value(font-size, 30, 14);
    padding: 1.27em 2.7em;
    border: none;
    border-radius: 1.87em;
    transition: 0.2s;
    cursor: pointer;
}

._orange {
    background: $orange;
}

._blue {
    background: $blue;
}
._green {
    background: $green;
}

.header {
    display: flex;
    @media (max-width: $md2 + px) {
        justify-content: center;
    }
    &__logo {
        background: rgba(255,255,255,0.8);
        @include adaptive-value(border-radius, 221,113);
        display: flex;
        align-items: center;
        color: $orange;
        @include adaptive-value(margin-top, 19, 15);
        @include adaptive-value(margin-bottom, 19, 15);
    }
    &__icon {
        @include adaptive-value(font-size, 90, 50);
        // margin-right: 0.28em;
    }
    &__text {
        font-family: ProximaNova-Bold;
        @include adaptive-value(font-size, 60, 30);
        line-height: 1.27em;
        padding: 0 0.42em;
    }
}

.section-first {
    background: url('../img/woman.png') no-repeat;
    background-position: left bottom;
    background-size: contain;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    @include adaptive-value(padding-top, 56, 0);
    @include adaptive-value(padding-bottom, 67, 15);
    @include adaptive-value(margin-bottom, 69, 15);
    @media (min-width: ($md1 + 1) + px) {
        background-size: calc(566 / 1436 * 100%);
    }
    @media (max-width: $md1 + px) {
        padding-left: 30%;
    }
    @media (max-width: $md4 + px) {
        padding-left: 0;
        background: none;
    }
    &__title {
        font-family: ProximaNova-Bold;
        @include adaptive-value(font-size, 100, 40);
        line-height: 1.22em;
        margin-bottom: 0.35em;
        @media (min-width: $md2 + px) {
            max-width: 70%;
        }
    }
    &__subtitle {
        font-family: ProximaNova-Light;
        @include adaptive-value(font-size, 40, 24);
        line-height: 48,72px;
        margin-bottom: 1.7em;
        @media (min-width: $md2 + px) {
            max-width: 40%;
        }
    }
}

.section-features {
    display: flex;
    @media (max-width: $md3 + px) {
        flex-wrap: wrap;
    }
    gap: 0.769em;
    @media (max-width: $md6 + px) {
        gap: 1.7em;
    }
    margin: 0 auto;
    @media (min-width: $md5 + px) {
        max-width: 70%;
        width: calc(1054 / 1463 * 100%);
    }
    @include adaptive-value(font-size, 26, 14);
    @include adaptive-value(margin-bottom, 117, 20);
    &__feature {
        display: flex;
        align-items: center;
        gap: 1.538em;
    }
    &__text {
        flex: 1 1 auto;
        font-family: ProximaNova-Light;
    }
    &__icon {
        flex: 1 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 2.308em;
        width: 1.5em;
        height: 1.5em;

        border-radius: 0.417em;
    }
}

.section-advantages {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    // margin-bottom: 85px;
    @include adaptive-value(margin-bottom, 85, 20);
    @media (min-width: $md5 + px) {
        max-width: calc(750 / 1463 * 100%);
    }
    @media (max-width: $md3 + px) {
        max-width: 70%;
    }
    @media (max-width: $md5 + px) {
        max-width: none;
    }
    &__title {
        font-family: ProximaNova-Bold;
        @include adaptive-value(font-size, 70, 26);
        text-align: center;
        @include adaptive-value(margin-bottom, 90, 20);
    }
    &__content {
        display: flex;
        flex-direction: column;
        @include adaptive-value(gap, 20, 10);
    }
}

.advantage {
    display: flex;
    align-items: center;
    gap: 1em;
    @include adaptive-value(font-size, 26, 14);
    &__icon {
        flex: 0 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
        font-size: 2.308em;
        width: 1.5em;
        height: 1.5em;
        border-radius: 0.417em;
        transform: rotate(-45deg);
        margin: 0.2em;
    }
    &__text {
        flex: 1 1 auto;
        font-family: ProximaNova-Light;
        & span {
            font-family: ProximaNova-Bold;
        }
    }
}

.section-try {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include adaptive-value(font-size, 70, 26);
    @include adaptive-value(margin-bottom, 85, 20);

    &__title {
        font-family: ProximaNova-Bold;
        text-align: center;
        padding: 0 1em;
        @include adaptive-value(margin-bottom, 136, 20);
        & span {
            color: $blue;
        }
    }
    &__roadmap {
        @include adaptive-value(margin-bottom, 100, 20);
    }
    &__subtitle {
        font-family: ProximaNova-Bold;
        font-size: 0.429em;
        color: #fff;
        text-align: center;
        background: $orange;
        padding: 0.667em 1.667em;
        border: 4px solid #fff;
        border-radius: 1.667em;
        @include adaptive-value(margin-bottom, 75, 20);
    }
}

.roadmap {
    display: flex;
    align-items: center;
    margin: 0 2.5em;
    @include adaptive-value(margin-bottom, 100, 20);
    @media (max-width: $md3 + px) {
        flex-direction: column;
        width: 40%;
        text-align: center;
    }
    
    &__text-container {
        flex: 1 1 30%;
        align-self: stretch;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }

    &__number {
        position: absolute;
        top: -2.5em;
        left: -2.5em;
        display: flex;
        justify-content: center;
        align-items: center;
        content: "1";
        font-family: ProximaNova-Bold;
        @include adaptive-value(font-size, 30, 14);
        width: 1.667em;
        height: 1.667em;
        color: #fff;
        border-radius: 50%;
        background: $orange;
        @media (max-width: $md3 + px) {
            top: 0;
        }
        &_second {
            content: "2";
            background: $green;
        }
        &_third {
            content: "3";
            background: $blue;
        }
    }

    &__text {
        width: fit-content;
        @include adaptive-value(font-size, 24, 14);
    }
    &__arrow {
        flex: 0 1 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 1em;
        @media (max-width: $md3 + px) {
            @include adaptive-value(height, 55, 20);
            padding: 1em 0;
        }
        & img {
            @include adaptive-value(width, 55, 20);
            @media (max-width: $md3 + px) {
                transform: rotate(90deg);
            }
        }
    }
}

.section-work {
    @include adaptive-value(margin-bottom, 100, 20);
    &__title {
        text-align: center;
        font-family: ProximaNova-Bold;
        @include adaptive-value(font-size, 70, 30);
        @include adaptive-value(margin-bottom, 80, 20);
    }
    &__content {
        display: grid;
        @include adaptive-value(font-size, 30, 16);
        gap: 1.333em;
        grid-template-columns: repeat(4, 1fr);
        @media (max-width: $md1 + px) {
            grid-template-columns: repeat(3, 1fr);
        }
        @media (max-width: $md3 + px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: $md5 + px) {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}

.work-card {
    @media (max-width: $md5 + px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__icon {
        font-size: 3em;
        color: $orange;
    }
    &__title {
        font-family: ProximaNova-Bold;
        padding: 1.5em 0;
    }
    &__text {
        font-size: 0.733em;
    }
}

.section-questions {
    margin: 0 auto;
    @include adaptive-value(font-size, 70, 30);
    &__title {
        font-family: ProximaNova-Bold;
        text-align: center;
        @include adaptive-value(margin-bottom, 60, 20);
    }
    &__content {
    }
}

.question {
    position: relative;
    &__body {
        display: flex;
        align-items: center;
        background: #EEEEEE;
        border-radius: 0.714em;
        padding: 0.229em 0.357em 0.229em 0.5em;
    }
    &:not(:last-child) {
        margin-bottom: 0.286em;
    }
    &__title {
        flex: 1 1 auto;
        font-family: ProximaNova-Bold;
        font-size: 0.35em;
    }
    &__open-answer {
        flex: 0 1 auto;
        border: 0;
        background: none;
        color: $green-add;
        font-size: 0.857em;
        cursor: pointer;
        transition: 0.2s;
        &:hover {
            color: $green-add-hover;
        }
        &._active {
            transform: rotate(45deg);
        }
    }
    &__answer {
        position: relative;
        top: -100%;
        transition: 0.2s;
        font-family: ProximaNova-Light;
        font-size: 0.35em;
        opacity: 0;
        height: 0;
        padding: 0 1.429em;
        &._active {
            top: 0;
            padding: 1.429em;
            height: auto;
            opacity: 1;
        }
    }
}